import React from 'react';
import {
    Row,
    Col,
    Card
} from 'react-bootstrap';
import cx from 'classnames';
import { Link } from 'react-router-dom';

const Feedback = (props) => {

    return (
        <Row className={cx("bg-gray d-flex justify-content-center px-4 admissionRow")}>
            <Col md={11}>
                <Row className={cx("d-flex justify-content-between")}>
                    <Col md={3}>
                        <a href="https://docs.google.com/forms/d/e/1FAIpQLSda_-gxhXvhtzuuaH2Drxsu0el0eFErVHmqoCH0NAxqILnykQ/viewform">
                            <Card className="academicUnit my-1">
                                <Card.Body className={cx("p-4")}>
                                    <Card.Title className={cx("text-center academicIcon")}>
                                        <i class="fa fa-user"></i>
                                    </Card.Title>
                                    <Card.Text className={cx("text-center text-primary2 font-weight-bold")}>
                                        Student Feeback 
                                    </Card.Text>
                                </Card.Body>
                            </Card>
                        </a>
                    </Col>
                    <Col md={3}>
                        <a href="https://docs.google.com/forms/d/e/1FAIpQLScSJlrIcqp4SWMlYIwq1ovNzR5K_zy38gWJpHyUlyF61mpgAw/viewform">
                            <Card  className="academicUnit my-1">
                                <Card.Body className={cx("p-4")}>
                                    <Card.Title className={cx("text-center academicIcon")}>
                                        <i class="fa fa-user"></i>
                                    </Card.Title>
                                    <Card.Text className={cx("text-center text-primary2 font-weight-bold")}>
                                        Parent Feedback
                                    </Card.Text>
                                </Card.Body>
                            </Card>
                        </a>
                    </Col>
                    <Col md={3}>
                        <a href="https://docs.google.com/forms/d/e/1FAIpQLSfo6hA6nKw5ROD_8e9IX8AZdfj3YL3PvytKFpY6b6zAts5nFQ/viewform">
                            <Card  className="academicUnit my-1">
                                <Card.Body className={cx("p-4")}>
                                    <Card.Title className={cx("text-center academicIcon")}>
                                        <i class="fa fa-user"></i>
                                    </Card.Title>
                                    <Card.Text className={cx("text-center text-primary2 font-weight-bold")}>
                                        Alumni Feedback
                                    </Card.Text>
                                </Card.Body>
                            </Card>
                        </a>
                    </Col>
                    <Col md={3}>
                        <a href="https://docs.google.com/forms/d/e/1FAIpQLSetNQQmvdkT1Ia71cji5acC3uKZJcB5_MJ4zW6deN5P3FIPtw/viewform">
                            <Card  className="academicUnit my-1">
                                <Card.Body className={cx("p-4")}>
                                    <Card.Title className={cx("text-center academicIcon")}>
                                        <i class="fa fa-user"></i>
                                    </Card.Title>
                                    <Card.Text className={cx("text-center text-primary2 font-weight-bold")}>
                                        Teacher Feedback
                                    </Card.Text>
                                </Card.Body>
                            </Card>
                        </a>
                    </Col>
                </Row>  
            </Col> 
        </Row>
    );
}

export default Feedback;